  .hor-menu .horizontalMenu > .horizontalMenu-list > li > a {
    &.active {
      color: $white;
      background: $white-1;
    }

    &:hover {
      color: $white;
      background: $white-1;
    }
  }
}

.dark-theme.horizontal-gradient .horizontalMenu > .horizontalMenu-list > li:hover > a {
  color: $white;
  background: transparent;
}

