/* =========== Toggle-menu CSS ============= */

/*Toggle-menu Color-css  */

.leftmenu-color {
  .first-sidemenu {
    background: $primary;
  }

  .second-sidemenu {
    background: $primary;
    border-left: 1px solid rgba(255, 255, 255, 0.04) !important;
  }

  .first-sidemenu li.active,
  .resp-vtabs .resp-tab-active:hover {
    background: $primary;
    border-left: 0 !important;
  }

  .app-sidebar.toggle-sidemenu .first-sidemenu li.active:before {
    background: #08daf5;
  }

  .first-sidemenu li {
    &.active svg,
    &:hover svg {
      fill: #08daf5;
    }
  }

  .resp-vtabs .resp-tabs-list li {
    border: 1px solid rgba(255, 255, 255, 0.04) !important;
    border-right: 0 !important;
    border-top: 0 !important;
    border-bottom: 0;
  }

  .second-sidemenu h5 {
    color: rgba(255, 255, 255, 0.88);
  }

  .first-sidemenu .side-menu__icon,
  .side-menu .slide.submenu a {
    color: rgba(255, 255, 255, 0.6);
    fill: rgba(255, 255, 255, 0.6);
  }

  .toggle-sidemenu {
    .side-menu__item:before,
    .slide-item:before {
      color: rgba(255, 255, 255, 0.88);
    }
  }

  &.app.sidebar-mini.sidenav-toggled .first-sidemenu li.active {
    border-left: 1px solid rgba(255, 255, 255, 0.04) !important;
  }

  .resp-vtabs .first-sidemenu {
    .resp-tabs-list li.active,
    .resp-tab-active li:hover {
      border-left: 0 !important;
    }
  }
}

/*Toggle-menu Dark-css  */

.leftmenu-dark {
  .first-sidemenu {
    background: #081e3e;
  }

  .second-sidemenu {
    background: #081e3e;
    border-left: 1px solid rgba(234, 232, 241, 0.07) !important;
  }

  .first-sidemenu li.active {
    background: #081e3e;
    border-left: 0 !important;
  }

  .resp-vtabs {
    .resp-tab-active:hover {
      background: #081e3e;
      border-left: 0 !important;
    }

    .first-sidemenu {
      .resp-tabs-list li.active,
      .resp-tab-active li:hover {
        border-left: 0 !important;
      }
    }

    .resp-tabs-list li {
      border: 1px solid rgba(234, 232, 241, 0.07) !important;
      border-right: 0 !important;
      border-top: 0 !important;
      border-bottom: 0;
    }
  }

  .second-sidemenu h5 {
    color: rgba(255, 255, 255, 0.88);
  }

  .first-sidemenu {
    li {
      &.active svg,
      &:hover svg {
        fill: $primary;
      }
    }

    .side-menu__icon {
      color: rgba(255, 255, 255, 0.6);
      fill: rgba(255, 255, 255, 0.6);
    }
  }

  .side-menu .slide.submenu a {
    color: rgba(255, 255, 255, 0.6);
    fill: rgba(255, 255, 255, 0.6);
  }

  .toggle-sidemenu {
    .side-menu__item:before,
    .slide-item:before {
      color: rgba(255, 255, 255, 0.88);
    }
  }

  &.app.sidebar-mini.sidenav-toggled .first-sidemenu li.active {
    border-left: 1px solid rgba(234, 232, 241, 0.07) !important;
  }
}

/*Toggle-menu Gradient-Color-css  */

.leftmenu-gradient {
  .first-sidemenu {
    background: linear-gradient(to top, #005bea 0%, #0db2de 100%) !important;
  }

  .second-sidemenu {
    background: linear-gradient(to top, #005bea 0%, #0db2de 100%) !important;
    border-left: 1px solid #1d97e1 !important;
  }

  .first-sidemenu li.active,
  .resp-vtabs .resp-tab-active:hover {
    background: rgba(0, 0, 0, 0.05);
    border-left: 0 !important;
  }

  .app-sidebar.toggle-sidemenu .first-sidemenu li.active:before {
    background: #08daf5;
  }

  .first-sidemenu li {
    &.active svg,
    &:hover svg {
      fill: #08daf5;
    }
  }

  .resp-vtabs .resp-tabs-list li {
    border: 1px solid rgba(234, 232, 241, 0.07) !important;
    border-right: 0 !important;
    border-top: 0 !important;
    border-bottom: 0;
  }

  .second-sidemenu h5 {
    color: rgba(255, 255, 255, 0.88);
  }

  .first-sidemenu .side-menu__icon,
  .side-menu .slide.submenu a {
    color: rgba(255, 255, 255, 0.6);
    fill: rgba(255, 255, 255, 0.6);
  }

  .toggle-sidemenu {
    .side-menu__item:before,
    .slide-item:before {
      color: rgba(255, 255, 255, 0.88);
    }
  }

  &.app.sidebar-mini.sidenav-toggled .first-sidemenu li.active {
    border-left: 1px solid rgba(234, 232, 241, 0.2) !important;
  }

  .resp-vtabs .first-sidemenu {
    .resp-tabs-list li.active,
    .resp-tab-active li:hover {
      border-left: 0 !important;
    }
  }
}

/*Toggle-menu light-Color-css  */
.dark-theme.leftmenu-light {
  .first-sidemenu,
  .second-sidemenu {
    background: $white;
  }

  .first-sidemenu li.active {
    background: $white;
    border-left: 0 !important;
  }

  .resp-vtabs {
    .resp-tab-active:hover {
      background: $white;
      border-left: 0 !important;
    }

    .resp-tabs-list li {
      border: 1px solid #eae8f1 !important;
      border-right: 0 !important;
      border-top: 0 !important;
      border-bottom: 0;
    }
  }

  .first-sidemenu li {
    &.active svg,
    &:hover svg {
      fill: $primary;
    }
  }

  .second-sidemenu h5 {
    color: #2c364c;
  }

  .first-sidemenu .side-menu__icon,
  .side-menu .slide.submenu a {
    color: #a8b1c7;
    fill: #5b6e88;
  }

  .first-sidemenu .side-menu__icon {
    color: #5b6e88;
  }

  .toggle-sidemenu {
    .slide-item {
      color: #5b6e88;
    }

    .side-menu__item:before,
    .slide-item:before {
      color: #6d7790;
    }
  }

  .second-sidemenu .app-sidebar__user .user-pro-body img {
    border: 2px solid #c9d2e8;
    background: $white;
  }

  &.app.sidebar-mini.sidenav-toggled .first-sidemenu li.active {
    border-left: 1px solid #eae8f1 !important;
  }

  .resp-vtabs .first-sidemenu {
    .resp-tabs-list li.active,
    .resp-tab-active li:hover {
      border-left: 0 !important;
    }
  }

  .app-sidebar.toggle-sidemenu {
    border-left: 0;
  }
}

